<template>
  <div v-loading="loading" class="c-layout">
    <!-- 顶部 -->
    <div class="c-mb20">
      <search v-model="form" @ok="getList(true)"></search>
      <el-divider></el-divider>
      <el-button type="primary" @click="ce()"> 新增 </el-button>
    </div>

    <!-- 表格 -->
    <el-table class="c-layout--flex c-overflow-hidden" :data="tableData" border height="100%">
      <el-table-column type="index" width="50"> </el-table-column>
      <el-table-column prop="name" label="姓名" width="180"> </el-table-column>
      <el-table-column prop="address" label="地址"> </el-table-column>
      <el-table-column prop="date" label="日期" width="180"> </el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="{ row, $index }">
          <el-button type="primary" @click="ce(row)"> 编辑 </el-button>
          <el-button type="danger" @click="del($index)"> 删除 </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <l-pagination
      class="c-mt20"
      style="text-align: right"
      :current-page.sync="page.currentPage"
      :page-size.sync="page.size"
      :total="page.total"
      @change="getList()"
    >
    </l-pagination>
  </div>
</template>

<script>
import search from "./search.vue"
export default {
  components: { search },
  data() {
    return {
      loading: false,
      form: {
        name: "",
        address: "",
      },
      tableData: [],
      page: {
        currentPage: 1, // 当前页
        size: 20, // 每页条数
        total: 0, // 总条数
      },
    }
  },
  created() {
    this.getList()
  },
  methods: {
    /** 新增编辑 */
    ce(row) {
      this.$LDialog(require("./ce.vue"), {
        title: row ? "编辑" : "新增",
        fatherItem: row,
        on: {
          refresh: () => {
            console.log("刷新方法")
          },
        },
      })
    },
    /** 获取列表 */
    getList(isRefresh = false) {
      if (isRefresh) {
        this.page.currentPage = 1
      }
      const params = {
        page: this.page.currentPage,
        size: this.page.size,
        ...this.form,
      }
      this.loading = true
      this.$api
        .get("/getList", params)
        .then((res) => {
          this.tableData = res.data.list
          this.page.total = res.data.total
        })
        .finally(() => {
          this.loading = false
        })
    },
    /** 删除 */
    del(index) {
      this.$confirm("确定删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.tableData.splice(index, 1)
          this.$message({
            type: "success",
            message: "删除成功!",
          })
        })
        .catch(() => {})
    },
  },
}
</script>
